* {
  box-sizing: border-box;
}

html {
  --columns: 2;
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

.people {
  grid-template-columns: repeat(var(--columns), 1fr);
  justify-content: center;
  column-gap: 10px;
  width: min-content;
  margin: 0 auto;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  cursor: pointer;
  width: 220px;
  padding: 10px;
  transition: transform 1s;
}

.person:hover {
  z-index: 1;
  background: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.person:hover img {
  border-radius: 60px;
}

.person:hover .extra {
  opacity: 1;
}

.photo {
  margin-bottom: 8px;
  position: relative;
}

.photo > img {
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
  display: block;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.details {
  text-align: center;
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}

.extra {
  opacity: 0;
  color: #000;
  margin-top: 8px;
  font-weight: 400;
}
